.app-wrapper {
  margin: 0 auto;
  display: grid;
  width: 1200px;
  min-height: 800px;

  grid-template-areas: "h h" "n c";

  grid-template-rows: 60px 1fr;
  grid-template-columns: 200px 10fr;
  grid-gap: 10px;
}

.app-wrapper-content {
  grid-area: c;
  background-color: #F4F4F4;
  border-radius: 16px 16px 0px 0px;
}
